import classes from "./MealsAvailable.module.css";
import Card from "../UI/Card";
import MealItem from "./MealItem/MealItem";

const DUMMY_MEALS = [
  {
    id: "m1",
    name: "Суши",
    description: "Лучшая рыба и овощи",
    price: 550,
  },
  {
    id: "m2",
    name: "Шницель",
    description: "Немецкая изюминка",
    price: 700,
  },
  {
    id: "m3",
    name: "Бургер Барбекю",
    description: "Американский, прожаренный, сытный",
    price: 450,
  },
  {
    id: "m4",
    name: "Салат",
    description: "Свежий... и зеленый...",
    price: 300,
  },
];

const MealsAvailable = () => {
  const mealsList = DUMMY_MEALS.map((meal) => (
    <MealItem
      id={meal.id}
      key={meal.id}
      name={meal.name}
      description={meal.description}
      price={meal.price}
    ></MealItem>
  ));

  return (
    <section className={classes.meals}>
      <Card>
        <ul>{mealsList}</ul>
      </Card>
    </section>
  );
};

export default MealsAvailable;
