import { useContext, useEffect, useState } from "react";
import CartIcon from "../Cart/CartIcon";
import CartContext from "../../store/cart-context";
import classes from "./HeaderCartButton.module.css";
import Cart from "../Cart/Cart";

const HeaderCartButton = (props) => {
  const [btnHighlited, setBtnHighlited] = useState(false);
  const cartContext = useContext(CartContext);
  const { items } = cartContext;

  const numberOfCartItems = items.reduce((currentNumber, item) => {
    return currentNumber + item.amount;
  }, 0);

  //Анимашка кнопки корзины при ререндере
  const buttonClasses = `${classes.button} ${btnHighlited ? classes.bump : ""}`;
  useEffect(() => {
    if (cartContext.items.length === 0) {
      return;
    }
    setBtnHighlited(true);

    //Удаление класса анимашки после срабатывания
    const timer = setTimeout(() => {
      setBtnHighlited(false);
    }, 300);

    //Чистка функции таймера, чтобы анимашка не обновлялась каждый раз при быстром нажатии
    return () => {
      clearTimeout(timer);
    };
  }, [items]);

  return (
    <button className={buttonClasses} onClick={props.onClick}>
      <span className={classes.icon}>
        <CartIcon />
      </span>
      <span>Корзина</span>
      <span className={classes.badge}>{numberOfCartItems}</span>
    </button>
  );
};

export default HeaderCartButton;
