import classes from "./MealsSummary.module.css";

const MealsSummary = () => {
  return (
    <section className={classes.summary}>
      <h2>Вкусная еда с доставкой до двери</h2>
      <p>Выберите свою любимую еду и вкусным ланчем или обедом дома.</p>
      <p>
        Все блюда приготовлены из свежих ингредиентов высококлавссными
        шеф-поварами.
      </p>
    </section>
  );
};

export default MealsSummary;
